<template>
  <transition name="fade"> 
    <div class="m-dialog-mask" :style="{zIndex}" v-if="show">
      <div class="m-dialog" :style="diaStyle" v-show="show">
        <span class="m-dialog-close-btn" @click="$emit('close')"> &times; </span>
        <div class="m-dialog-header">
          <slot name="header">{{title}}</slot>
        </div>
        <div class="m-dialog-body">
          <slot></slot>
        </div>
        <div class="m-dialog-footer">
          <slot name="footer"> </slot>
        </div>
      </div> 
    </div> 
  </transition>
</template>
<script>
  export default {
    name:'mDialog',
    props:{
      show:{
        type:Boolean,
        default:false
      },
      title:{
        default:""
      },
      width:{
        type:String,
        default:'60vw'
      },
      zIndex:{
        type:Number,
        default:10
      }
    },
    computed:{
      diaStyle(){
        return {
          width:`${this.width}`
        }
      }
    }
  }
</script>
<style lang="less" scoped>
  .m-dialog-mask{
    position:fixed;
    z-index: 9;
    top:0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #00000066;
    &.fade-enter,&.fade-leave-to{
      opacity: 0;
      .m-dialog{
        transform: translate(-50%,-60%);
      }
    }
    &.fade-enter-active,&.fade-leave-active{
      transition: all 240ms ease-out;
      .m-dialog{
        transition: all 240ms ease-out;
      }
    }
  }
  .m-dialog{ 
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    min-width: 400px;
    min-height: 300px;
    background-color: #fff;
    max-height: 90vh;
    max-width: 90vw;
    display: flex;
    flex-direction: column;
    .m-dialog-close-btn{
      width: 28px;
      height: 27px;
      position: absolute;
      right: 1px;
      top: 1px;
      color:#f2f2f2;
      cursor: pointer;
      user-select: none;
      text-align: center;
      line-height: 24px;
      font-size: 24px;
      &:hover{
        background-color: #999;
        color:#3F51B5;
      }
      &:active{
        box-shadow: inset 1px 1px 3px 2px #44444455;
        background-color: #888;
      }
    }
    .m-dialog-header{
      height: 30px;
      line-height: 30px;
      padding:0 10px;
      font-size: 16px;
      background-color: #3F51B5;
      color:#fefefe;
    }
    .m-dialog-body{
      background-color: #f2f2f2;
      flex:1;
      overflow: hidden auto;
      padding: 10px; 
      &::-webkit-scrollbar  
      {  
          width: 6px;  
          height: 6px;  
          background-color: #F5F5F5;  
      }  
        
      &::-webkit-scrollbar-track  
      {  
          -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
          border-radius: 4px;  
          background-color: #F5F5F5;  
      }  
      &::-webkit-scrollbar-thumb  
      {  
          border-radius: 4px;  
          -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
          background-color: #e8edfa80;  
      }   
    }
    .m-dialog-footer{
      padding: 0px 20px;
    }
  }
</style>